import React from 'react';

//images
import image1 from '../images/food-1.jpg';
import image2 from '../images/food-2.jpg';
import image3 from '../images/food-3.jpg';
import image4 from '../images/food-4.jpg';
import image5 from '../images/food-5.jpg';
import image6 from '../images/food-6.jpg';


const Food = () => {
    return (
        <div className="bg">
            <div className="container food">
                <h1>Food</h1>

                <div class="row">
                    <div class="col-4">
                        <img src={image1} className="w-100" />
                    </div>
                    <div class="col-4">
                        <img src={image2} className="w-100" />
                    </div>
                    <div class="col-4">
                        <img src={image3} className="w-100" />
                    </div>
                    <div class="col-6">
                        <img src={image4} className="w-100" />
                    </div>
                    <div class="col-6">
                        <img src={image5} className="w-100" />
                    </div>
                    <div class="col-6">
                        <img src={image6} className="w-100" />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Food;